.admin-view {
    margin: 20px;
  }
  
  .admin-heading {
    font-size: 24px;
    margin-bottom: 10px;
  }
  
  .user-table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .user-table th,
  .user-table td {
    padding: 10px;
    border-bottom: 1px solid #ddd;
  }
  
  .user-table th {
    text-align: left;
  }
  
  .delete-button {
    background-color: #f44336;
    color: white;
    border: none;
    padding: 8px 16px;
    cursor: pointer;
    border-radius: 4px;
  }
  
  .delete-button:hover {
    background-color: #d32f2f;
  }
  